@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html, body {
        @apply bg-gray-900 text-slate-400

    }
    
    a {
        @apply cursor-pointer;
    }

    a:hover {
        @apply text-purple-800
    }
}

@layer components {
    .contact-input {
        @apply h-[50px] bg-slate-600 text-base border border-purple-800 px-5 w-full m-1
    }

    .contact-li {
        @apply overflow-hidden w-full
    }

    .contact-li-half {
        @apply min-w-[49%] block float-left overflow-hidden
    }

    .skewed-border-right {
        -webkit-transform: skewX(-10deg);
        -moz-transform: skewX(-10deg);
        -ms-transform: skewX(-10deg);
        transform: skewX(-10deg);
        @apply border-purple-800 border
    }

    .skewed-border-left {
        -webkit-transform: skewX(10deg);
        -moz-transform: skewX(10deg);
        -ms-transform: skewX(10deg);
        transform: skewX(10deg);
        @apply border-purple-800 border
    }

    .navActive {
        @apply text-purple-800
    }
}


